.mod.addr {
  display: inline-block;
}
.inputBox {
  padding: 1em;
}
.inputBox label {
  margin-right: 0.2em;
}
#name label.mod {
  padding-left: 0.3em;
}
#rescan {
  font-weight: bold;
}
#modOverlay {
  width: 100%;
  height: 100%;
  right: 0.3em;
  bottom: 0.3em;
  pointer-events: none;
  position: fixed;
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-end;
  z-index: 100;
}
.panel {
  pointer-events: initial;
  padding: 0.5em;
  margin: 0.2em;
  overflow: auto;
  max-width: 80%;
  max-height: 80%;
}
.panel td {
  padding-left: 0.4em;
  padding-right: 0.4em;
}
#toolbox {
  font-weight: bold;
}
#toolbox span {
  display: none;
}
#toolbox a:before {
  content: ' [';
}
#toolbox a:after {
  content: '] ';
}
